body {
  background: #f5f5f5;
}
.App {
  text-align: center;
}
.title_logo{
  display: flex;
  justify-content: center;
  align-items: center;
}
.logoTodo{
  width: 55px;
  height: 55px;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
 
.wrapper_content {
  width: 35%;
  margin: 0 auto;
  border: 1px solid #dcdbdb;
  box-shadow: 3px 3px 10px 2px #efefef;
  margin-bottom: 150px;
  background-color: #fff;
} 
h1 {
  font-size: 100px;
  margin: 0;
  font-weight: 500;
  color: #efb7b7;
}

